/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import CartLineItems from "../lineitems"
import { useCartState } from "../_state/hooks"
import { useShopState, useShopDispatch } from "../../shop/_state/hooks"
import { IconLogo } from "Common/icons/icon-remove.jsx"
import { useCartToggle } from "Hooks"

function CartContents() {
  const cartState = useCartState()
  const shopState = useShopState()
  const shopDispatch = useShopDispatch()

  useCartToggle(shopDispatch)

  const CartTitleCSS = css`
    top: 48%;
    text-align: center;
    color: #ddd;
    position: absolute;
    margin: 0;
    width: 100%;
    font-size: 22px;
    font-weight: normal;
    margin: 0;
    text-align: center;
  `

  const CartContentsCSS = css`
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
    position: relative;
    flex: 1;
    transition: all 0.2s ease;
    opacity: ${shopState.isCartUpdating ? "0.3" : "1"};
    filter: ${shopState.isCartUpdating ? "blur(2px)" : "none"};
    padding-top: 20px;

    > .wps-notice {
      position: relative;
      top: 30%;
    }

    .wps-cart-lineitem {
      border-bottom: 1px solid #ddd;
      padding-bottom: 20px;
      margin-bottom: 20px;
    }

    > div:last-child {
      border-bottom: none;
      padding-bottom: 0;
    }

    &[data-is-cart-empty="true"] .wps-notice-info {
      background: none;
      border: none;
      font-size: 24px;
    }

    svg {
      width: auto;
      height: 69px;
      position: absolute;
      top: -64px;
      left: 149px;
    }
  `

  return (
    <section
      className="wps-cart-contents"
      data-is-cart-empty={cartState.isCartEmpty}
      css={CartContentsCSS}
    >
      {cartState.isCartEmpty && !shopState.isCartUpdating ? (
        <h2 css={CartTitleCSS}>
          <IconLogo color="#dedede" />

          {cartState.settings.emptyCartText}
        </h2>
      ) : shopState.cartData.lines && shopState.cartData.lines.edges.length ? (
        <CartLineItems />
      ) : null}
    </section>
  )
}

export default CartContents
